    TID_SHIP_CERB_FIGHTER: "Sentinelle cerbre",
    TID_SHIP_CERB_FIGHTER_DESCR: "Les sentinelles ont une coque assez faible, mais un puissant armement. individuellement elles ne reprsentent pas une grande menace, mais elles sont extrmement dangereuses en grand nombre ou bien appuyes par des vaisseaux cerbres plus puissants.\\n\\nles sentinelles restent toujours dans leur secteur d'affectation.",
