    TID_SHIP_ALPHA_DRONE: "Drone alpha",
    TID_SHIP_ALPHA_DRONE_DESCR: "Un vaisseau de combat automatis, rapide, mais faible. le drone se rend  sa destination et y reste jusqu' ce qu'il soit dtruit ou que son minuteur d'activation soit coul.",
    TID_SHIP_SHIPMENT_DRONE: "Drone de cargaison",
