    TID_SHIP_CERBERUS_BOMBER: "Bombardier cerbre",
    TID_SHIP_CERBERUS_BOMBER_DESCR: "Un puissant vaisseau qui tire une roquette  intervalles rguliers sur tous les joueurs participants qui possdent des vaisseaux dans le secteur o le bombardier se trouve, ou dans les secteurs adjacents. \\n\\nce vaisseau restera toujours dans son secteur d'affectation, mais les roquettes peuvent tre tires sur les vaisseaux en approche qui se trouvent dans les secteurs adjacents.",
    TID_SHIP_STORM: "Tempte cerbre",
    TID_SHIP_STORM_DESCR: "Ce vaisseau suit toujours un chemin circulaire au sein de son secteur. il est quip d'une puissante arme de longue porte qui lance  intervalle rgulier des missiles dart rapides contre tous les vaisseaux ennemis  sa porte.",
    TID_SHIP_CERB_GHOSTS: "Fantmes cerbres",
    TID_SHIP_CERB_GHOSTS_DESCR: "Un groupe de vaisseaux rapides qui saute de manire imprvisible et se dplace vers la cible la plus proche dans le systme stellaire.\\n\\nles vaisseaux fantmes sont immuniss contre tous les modules qui n'infligent pas de dgts, et ne sont pas considrs comme des vaisseaux par certains modules tels que barrage et dmantlement.",
    TID_CORP_FLAGSHIP: "Vaisseau amiral de la corporation",
    TID_CORP_FLAGSHIP_DESCR: "Ce vaisseau appartient  une corporation et ne peut tre contrl que par les officiers qui en font partie.\\n\\nun vaisseau amiral peut parfois assister sa corporation dans une mission d'toile blanche.",
    TID_MODULE_TRADER: "Bonus d'change",
