    TID_MODULE_TRANSPORT_CAPACITY: "Extension de l'aire de chargement",
    TID_MODULE_TRANSPORT_CAPACITY_DESCR: "Augmente la capacit de stockage totale du vaisseau de transport, ce qui vous permet de transporter plus de cargaisons ou d'artfacts  la fois.",
    TID_MODULE_MINING_BOOST: "Bonus de forage",
    TID_MODULE_MINING_BOOST_DESCR: "Confre un bonus temporaire  la vitesse de rcupration d'hydrogne.",
    TID_MODULE_EMP: "IEM",
    TID_MODULE_EMP_DESCR: "Dsactive tous les vaisseaux, roquettes et stations cerbre dans la zone touche pendant une courte priode de temps, y compris le vaisseau ayant dclench l'iem. les vaisseaux dsactivs ne peuvent plus ni bouger ni tirer, mais peuvent toujours activer les modules.",
    TID_MODULE_SHIPMENT_COMPUTER: "I.a. de cargaison",
    TID_MODULE_SHIPMENT_COMPUTER_DESCR: "Rcupre automatiquement les cargaisons des points de virage intermdiaires si la destination de ces cargaisons correspond  l'un des points de virage suivants du vaisseau. les cargaisons rcupres automatiquement ajoutent un bonus  leur rcompense.\\n\\nune fois ce module dbloqu, il suffit de slectionner n'importe quel vaisseau pour rvler la destination des cargaisons.",
    TID_MODULE_REPAIR: "Rparation  distance",
    TID_MODULE_WEAK_SHIELD: "Bouclier alpha",
    TID_MODULE_SHIELD: "Bouclier delta",
    TID_MODULE_STRONG_SHIELD: "Bouclier omega",
