    TID_MODULE_SHIELD_WITH_SPEED_DESCR: "Absorbe les dgts reus jusqu' son puisement ou bien la fin du minuteur d'activation. le vaisseau propritaire se dplace plus vite lorsque le bouclier est actif.",
