    TID_MODULE_TIME_WARP: "Distorsion temporelle",
    TID_MODULE_TIME_WARP_DESCR: "Acclre le temps  proximit du vaisseau. modifie les dures de voyage ainsi que la vitesse de combat et de rcupration d'artfact.\\n\\nun objet  la porte de plusieurs distorsions temporelles n'est affect que par ceux au niveau lev.",
