    TID_MODULE_LASER_DESCR: "Puissante arme qui inflige des dgts grandissants  mesure qu'elle reste fixe sur la mme cible.",
    TID_MODULE_WEAK_BATTERY: "Canon faible",
    TID_MODULE_WEAK_BATTERY_DESCR: "Une arme faible qui inflige des dgts  une cible unique  la fois.",
    TID_MINERAL_STORAGE_CAPACITY: "Extension de stockage d'hydrogne",
    TID_MINERAL_STORAGE_CAPACITY_DESCR: "Augmente la quantit totale d'hydrogne que le vaisseau de forage peut transporter.",
