    TID_MODULE_REDSTAR_EXTENDER_DESCR: "Ajoute une petite quantit de vie  une toile rouge lorsqu'elle est active prs d'elle.",
    TID_MODULE_STEALTH: "Discrtion",
    TID_MODULE_STEALTH_DESCR: "Le vaisseau qui l'active esquive la totalit des tirs des vaisseaux ennemis. il ne tire sur aucun ennemi et ne peut tre touch par personne pendant la dure de l'effet.",
    TID_MODULE_MASS_MINING: "Forage  distance",
    TID_MODULE_MASS_MINING_DESCR: "Fore simultanment l'hydrogne de toutes les ceintures d'astrodes dans le mme secteur, au cot d'une vitesse rduite.",
    TID_MODULE_UNITY: "Unit",
    TID_MODULE_UNITY_DESCR: "Augmente les dgts par seconde pour le vaisseau propritaire si d'autres joueurs allis ont des vaisseaux dans le mme secteur. l'augmentation est proportionnelle au nombre d'autres joueurs allis ayant des vaisseaux dans le secteur.",
