    TID_MODULE_UNITY_MINER_DESCR: "Augmente la vitesse de forage pour le vaisseau actif, si les autres joueurs forent dans la mme zone. l'augmentation est proportionnelle au nombre de joueurs qui forent dans le secteur.",
    TID_MODULE_FORTIFY: "Fortification",
    TID_MODULE_FORTIFY_DESCR: "Rduit les dgts directs reus par les armes. tous les vaisseaux cerbre dans le secteur se dplacent vers leur propritaire. les bases et vaisseaux ennemis concentrent leurs attaques sur le propritaire avant toute autre cible potentielle.",
    TID_MODULE_SALVAGE: "Sauvetage",
    TID_MODULE_SALVAGE_DESCR: "Rtablit instantanment un pourcentage donn de l'intgrit de la coque chaque fois qu'un vaisseau (autre que le fantme cerbre) est dtruit dans le mme secteur.",
    TID_MODULE_SUPRESS: "Suppression",
    TID_MODULE_SUPRESS_DESCR: "Empche un vaisseau ennemi unique de tirer pour une courte priode de temps.",
    TID_MODULE_SHIPMENT_DRONE: "Drone de cargaison",
    TID_MODULE_SHIPMENT_DRONE_DESCR: "Envoie un drone qui rcuprera et livrera automatiquement des cargaisons quand cela est possible. le drone ne peut traverser que des secteurs qui sont lis par une ligne de saut active.\\n\\nle drone ne peut charger des cargaisons que vers une seule destination  la fois. lorsqu'il dpose plus d'une cargaison, la livraison entire rapportera une rcompense de crdits bonus proportionnelle au nombre de cargaisons supplmentaires livres.",
    TID_MODULE_DESTINY: "Destine",
    TID_MODULE_DESTINY_DESCR: "Inflige de lourds dgts de zone  tous les ennemis  porte. le vaisseau dclenchant l'attaque saute instantanment vers un astrode alatoire du systme stellaire. si aucun astrode n'est disponible, destine ne peut pas tre activ.\\n\\naprs le saut, le vaisseau reste dsactiv pendant un court laps de temps.\\n\\nne peut tre activ que si des vaisseaux ennemis se trouvent dans le secteur.",
    TID_MODULE_LEAP: "Bond",
    TID_MODULE_LEAP_DESCR: "Le vaisseau propritaire amorce sa tlportation vers le vaisseau alli en combat le plus proche, o qu'il soit dans le systme stellaire actuel. une fois le dlai d'activation termin, et si l'alli vis est encore en vie, le vaisseau propritaire se tlporte vers l'objet le plus proche du vaisseau alli. aprs la tlportation, le vaisseau propritaire et le vaisseau alli sont tous les deux dsactivs pour un court laps de temps.",
    TID_MODULE_BOND: "Lien",
    TID_MODULE_BOND_DESCR:
      "Cre un lien de dplacement avec le vaisseau ennemi cibl. tant que le lien est actif, le vaisseau ennemi ne peut pas se dplacer de faon indpendante. tout dplacement du vaisseau propritaire sera imit par le vaisseau ennemi, afin que la distance entre les deux vaisseaux reste toujours la mme.\\n\\nsi le vaisseau ennemi active un champ de barrire, le vaisseau propritaire ne sera pas affect par celui-ci.\\n\\nle vaisseau ennemi peut se librer du lien de faon prcoce en utilisant lien sur le vaisseau d'origine, ou en utilisant un module provoquant une tlportation.\\n\\nne fonctionne pas sur les toiles rouges publiques de niveau7 ou infrieur. un vaisseau qui utilise l'influence de lien sur un cerbre ne peut pas se tlporter vers une toile rouge publique.\\n\\nne fonctionne pas contre les vaisseaux tempte cerbre.",
    TID_MODULE_VENGEANCE: "Vengeance",
    TID_MODULE_VENGEANCE_DESCR: "Commence  s'activer automatiquement lorsque le vaisseau propritaire tombe en dessous du seul d'intgrit de coque. une fois que le dlai d'activation est coul, et si le vaisseau propritaire n'est pas encore dtruit, une norme explosion infligera des dgts de zone  tous les ennemis  porte.",
    TID_MODULE_PASSIVE_SHIELD: "Bouclier passif",
    TID_MODULE_PASSIVE_SHIELD_DESCR: "Un bouclier toujours actif qui se rgnre perptuellement.\\n\\nle bouclier ne peut commencer  se rgnrer que lorsqu'il ne reoit plus de dgts pendant une dure quivalente au dlai de rgnration.",
    TID_MODULE_MIRROR_SHIELD: "Bouclier miroir",
    TID_MODULE_MIRROR_SHIELD_DESCR: "Renvoie une partie des dgts perus  l'assaillant.",
    TID_MODULE_AREA_SHIELD: "Bouclier zone",
    TID_MODULE_AREA_SHIELD_DESCR: "Absorbe les dgts infligs aux coques de tous les vaisseaux allis dans la zone d'effet, vaisseaux de transport et de forage inclus.",
    TID_MODULE_BLAST_SHIELD: "Bouclier balistique",
    TID_MODULE_BLAST_SHIELD_DESCR: "Protge le vaisseau propritaire et tous les vaisseaux allis  porte contre les dgts de zone uniquement. les dgts directs infligs par les armes ne sont pas absorbs par ce bouclier et sont donc appliqus directement  la coque du vaisseau.",
    TID_MODULE_BARRIER: "Barrire",
    TID_MODULE_BARRIER_DESCR: "Cre un champ de force autour du vaisseau. les vaisseaux et les roquettes ennemis ne peuvent pas bouger lorsqu'ils sont dans ce champ de force. les vaisseaux ennemis ne peuvent pas utiliser d'objet en tant que cible du module de tlportation dans la zone d'effet.",
    TID_MODULE_IMPULSE: "Impulsion",
    TID_MODULE_IMPULSE_DESCR: "Confre un petit bonus de vitesse  un vaisseau qui lui permet de voyager plus rapidement sur sa trajectoire actuelle.",
    TID_MODULE_BARRAGE: "Barrage",
    TID_MODULE_BARRAGE_DESCR: "Un armement qui inflige des dgts supplmentaires  sa cible unique lorsqu'il y a plusieurs vaisseaux ennemis dans le secteur.\\n\\nles fantmes cerbres ne sont pas considrs comme des ennemis et n'engendrent pas de dgts supplmentaires.",
    TID_MODULE_HYDRO_UPLOAD: "Transfert d'hydrogne",
    TID_MODULE_HYDRO_UPLOAD_DESCR: "Transfre instantanment un pourcentage de l'hydrogne collect aux rserves de votre empire sans devoir retourner sur une plante.",
    TID_MODULE_TRADE_BURST: "Pousse d'changes",
    TID_MODULE_TRADE_BURST_DESCR: "Compte les cargaisons livres durant la priode d'activation. une fois un seuil minimum atteint, chaque nouvelle cargaison de crdits livre par le vaisseau propritaire pendant que le module est actif rapporte une rcompense bonus.",
    TID_MODULE_SHIPMENTBEAM: "Rayon de cargaisons",
    TID_MODULE_SHIPMENTBEAM_DESCR: "Charge instantanment les cargaisons  la plus faible valeur depuis n'importe quel avant-poste cible du systme stellaire. ne fonctionne pas avec les artfacts ou reliques.",
    TID_MODULE_SANCTUARY: "Sanctuaire",
    TID_MODULE_SANCTUARY_DESCR: "Lorsque le vaisseau propritaire est sur le point d'tre dtruit durant un combat ou une supernova, il retourne dans son systme d'origine. toute cargaison  bord du vaisseau sera perdue.\\n\\nles cuirasss qui sont dtruits sur une toile rouge et qui sont quips de sanctuaire ne pourront pas revenir sur la mme toile.",
    TID_MODULE_CRUNCH: "Pnurie",
    TID_MODULE_CRUNCH_DESCR: "Collecte instantanment l'hydrogne de tous les champs d'astrodes du secteur actuel (limit par la quantit max et la capacit totale du vaisseau de forage).",
    TID_MODULE_RECALL: "Rappel",
