    TID_MODULE_ALPHAROCKET: "Roquette alpha",
    TID_MODULE_ALPHAROCKET_DESCR: "Une roquette rapide mais de faible puissance pouvant tre tire sur une cible fixe unique. cette roquette explose et inflige des dgts de zone  tous les vaisseaux ennemis  l'impact.\\n\\nles vaisseaux ennemis peuvent tirer sur la roquette selon les rgles de ciblage normales. aucun dgt n'est appliqu si la roquette est dtruite avant d'arriver  destination.",
    TID_MODULE_DELTAROCKET: "Roquette delta",
