      "Une roquette extrmement rapide qui inflige des dgts de zone  tous les vaisseaux ennemis  l'impact. le propritaire peut galement slectionner un deuxime point de virage aprs le tir de la roquette, s'il le souhaite.\\n\\nles vaisseaux ennemis peuvent tirer sur la roquette selon les rgles de ciblage normales. aucun dgt n'est appliqu si la roquette est dtruite avant d'arriver  destination.\\n\\nne fonctionne pas dans les secteurs avec des portes de saut sur les toiles blanches.",
    TID_MODULE_EMP_ROCKET: "Roquette iem",
    TID_MODULE_EMP_ROCKET_DESCR: "Une roquette qui dsactive tous les vaisseaux proches au moment de l'impact.",
    TID_MODULE_OMEGAROCKET: "Roquette omega",
    TID_MODULE_OMEGAROCKET_DESCR: "Une roquette  courte porte puissante, mais lente, qui inflige d'importants dgts de zone  tous les vaisseaux ennemis  l'impact.\\n\\nles vaisseaux ennemis peuvent tirer sur la roquette selon les rgles de ciblage normales. la roquette infligera quand mme de faibles dgts de zone dans un plus petit rayon d'effet aux vaisseaux ennemis si elle est neutralise avant d'avoir atteint sa destination.",
    TID_MODULE_ENRICH: "Enrichissement",
    TID_MODULE_ENRICH_DESCR: "Augmente instantanment la quantit d'hydrogne stocke sur tous les astrodes dans le secteur. cette fonction ne peut pas tre utilise dans le mme secteur plus d'une fois par jour.",
    TID_MODULE_RUSH: "Rue",
    TID_MODULE_RUSH_DESCR: "Augmente la vitesse du vaisseau propritaire de transport si celui-ci transporte des cargaisons destines au prochain point de virage du vaisseau. l'augmentation de la vitesse est proportionnelle au nombre de cargaisons destines au point de virage suivant.",
    TID_MODULE_GENESIS: "Gense",
    TID_MODULE_GENESIS_DESCR: "Cre instantanment de nouveaux champs d'astrodes dans le secteur o se trouve le vaisseau de forage.\\n\\nne fonctionne pas dans les secteurs avec des portes de saut sur les toiles blanches.",
    TID_MODULE_ENTRUST: "Confiance",
    TID_MODULE_ENTRUST_DESCR: "Permet au vaisseau de transport propritaire de tlporter instantanment sa cargaison vers un autre vaisseau de transport  porte. fonctionne avec les cargaisons et les reliques et artfacts entirement chargs.",
    TID_MODULE_OFFLOAD: "Dchargement",
    TID_MODULE_OFFLOAD_DESCR: "Lorsqu'il est actif, ce module permet au vaisseau de transport de livrer toutes ses cargaisons ds son arrive  une station d'change, mme s'il ne s'agit pas de la vritable destination de celles-ci. les cargaisons n'tant  l'origine pas destines  cette station d'change rapporteront une rmunration moins importante.",
    TID_ALPHA_DRONE_MODULE_DESCR: "Lance un drone de combat unique vers n'importe quelle destination au sein du systme stellaire. le drone se rend automatiquement  la destination et y reste jusqu' ce qu'il soit dtruit ou que son minuteur d'activation soit coul.\\n\\nne fonctionne pas sur les secteurs avec des portes de saut sur les toiles blanches.",
    TID_MODULE_MINING_DRONE: "Drone de forage",
    TID_MODULE_MINING_DRONE_DESCR:
      "Envoie un vaisseau indpendant vers l'astrode cibl. ce dernier doit tre  porte du vaisseau de forage. le drone continuera le processus de forage sur l'astrode jusqu' ce que ce dernier soit puis ou que les capacits maximales de stockages du drone soient atteintes. une fois le processus de forage termin, le drone ira dposer l'hydrogne sur la plante la plus proche.\\n\\nne fonctionne pas dans les secteurs avec des portes de saut sur les toiles blanches.",
    TID_MODULE_DART_LAUNCHER: "Lance-missiles dart",
    TID_MODULE_DART_LAUNCHER_DESCR:
      "Lance un missile dart contre le vaisseau ennemi vis  intervalles rguliers.\\n\\nle missile peut tre neutralis par un vaisseau ennemi en suivant les rgles de ciblage normales.\\n\\nquand le missile atteint sa destination, il inflige des dgts directs par arme au vaisseau vis uniquement, et uniquement si le vaisseau vis est toujours dans la zone d'impact.\\n\\ncette arme ne peut tirer que sur des vaisseaux ennemis. les roquettes et stations cerbre sont ignores.",
    TID_MODULE_DISPATCH: "Expdition",
    TID_MODULE_DISPATCH_DESCR: "Le vaisseau de transport initie sa tlportation vers la plante qui contient le plus d'objets (cargaisons, artfacts ou reliques) dans le systme stellaire. si plusieurs plantes contiennent le mme nombre d'objets, c'est la plante la plus loigne qui est choisie.\\n\\nla tlportation se produit une fois que le temps de prparation s'est coul. le temps de prparation est proportionnel au nombre d'objets sur la plante vise au moment de l'activation.",
    TID_MODULE_HYDRO_ROCKET: "Roquette d'hydrogne",
    TID_MODULE_HYDRO_ROCKET_DESCR: "Permet au vaisseau de forage de tirer une puissante roquette n'importe o dans le systme stellaire.\\n\\npour l'activer, le vaisseau de forage doit avoir rcupr au moins la quantit d'hydrogne requise. l'hydrogne est consomm ds l'activation.",
    TID_MODULE_SUSPEND: "Suspension",
    TID_MODULE_SUSPEND_DESCR: "Ralentit le temps dans une grande zone autour du vaisseau. ignore tous les effets de distorsion temporelle dans la zone d'effet.\\n\\nun objet  la porte de plusieurs suspensions n'est affect que par celui ayant le niveau le plus lev.",
    TID_MODULE_RECOIL: "Recul",
    TID_MODULE_RECOIL_DESCR: "Un dcompte de tlportation commence pour le vaisseau amiral avant de l'envoyer n'importe o dans le systme stellaire. lorsque le dcompte touchera  sa fin, le vaisseau amiral sautera vers sa destination et dclenchera immdiatement un deuxime dcompte de tlportation qui le renverra  l'endroit d'o il venait.",
    TID_MODULE_IMMOLATION: "Immolation",
    TID_MODULE_IMMOLATION_DESCR: "Dtruit instantanment tous les vaisseaux cerbres dans le secteur.",
    TID_CERB_STATION_HOME2: "Base cerbre",
    TID_PLANET_DESERT_1: "Petite plante dsertique",
    TID_PLANET_DESERT_2: "Plante dsertique",
    TID_PLANET_DESERT_3: "Grande plante dsertique",
    TID_PLANET_DESERT_DESCR: "Les plantes dsertiques produisent de faibles cargaisons mais fournit un grand espace de stockage d'hydrogne.",
    TID_PLANET_LAVA_1: "Petite plante incandescente",
    TID_PLANET_LAVA_2: "Plante incandescente",
