    TID_PLANET_WATER_DESCR: "Planetas aquticos produzem carregamentos com recompensas razoveis e fornecem uma renda horria de crditos ao seu imprio.",
    TID_PLANET_TERRAN_1: "Planeta terrestre pequeno",
