    TID_PLANET_TERRAN_DESCR: "Planetas terrestres produzem carregamentos com recompensas medianas e fornecem uma pequena renda horria de hidrognio ao seu imprio.",
    TID_PLANET_GAS_1: "Planeta gasoso",
    TID_PLANET_GAS_2: "Gigante gasosa",
    TID_PLANET_GAS_DESCR: "Planetas gasosos produzem carregamentos com recompensas medianas e fornecem uma pequena renda horria de crditos ao seu imprio.",
    TID_PLANET_ICE: "Planeta glido",
